/*********************
BREAKPOINTS
*********************/
/*Element styles to allow sticky footer and sticky header to be friends */
html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: 'Fira', Helvetica, Arial, Verdana, sans-serif;
  line-height: auto;
}

.header {
  display: none;
}

.empty {
  display: none !important;
}

.navigation {
  padding: 10px 0;
  margin: 0px auto;
  background-color: #DC6328;
  min-height: 70px;
  /*width: 100vw;*/
  width: 100%;
  border-top: solid 3px black;
  box-shadow: 2px 2px 10px 0px #000;
  z-index: 1000;
  transition: 0.7s all;
  opacity: 1;
}
@media (max-width: 835px) {
  .navigation {
    height: 80px;
    padding: 0 0 20px;
  }
}

.region-navigation {
  max-width: 1200px;
  margin: 0 auto;
}

#page {
  display: table;
  max-height: 100%;
  table-layout: fixed;
  /*Fixes firefox bug that disallows responsive resizing of images within table elements*/
}

#comments_wrapper {
  height: 100%;
}

#content {
  height: 100%;
}

#content-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1em;
}

#highlighted {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-justify-content: center;
      -ms-flex-pack: center;
          justify-content: center;
  text-align: left;
  /*min-height: calc(100vh - 120px);*/
  /*max-height: 800px;*/
  background-color: #ffffff;
  width: 100%;
  clear: both;
  overflow: hidden;
  z-index: 100;
}
#highlighted .region-highlighted {
  width: 100vw;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-wrap: wrap;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
  padding: 0 1em 4px;
}
#highlighted .region-highlighted p {
  margin: 1.5em .5em;
}
#highlighted .region-highlighted img {
  /*object-position: 50% 0%;
  object-fit: cover;
  width: auto;
  max-height: 90vh;
  min-height: 600px;
  overflow: hidden;*/
}

#main {
  overflow: hidden;
  background-color: #cccccc;
  padding-top: 1em;
  width: 100%;
  min-height: 100vh;
  height: auto !important;
  height: 100%;
  padding-bottom: 14em;
}

/*Basic typography */
p {
  font-family: 'Fira', Helvetica, Arial, Verdana, sans-serif;
  line-height: auto;
}

img {
  max-width: 100% !important;
  padding: 0.5em;
}

h1, h2, h2 a, h3, h4, h5 {
  font-family: 'Fira', Helvetica, Arial, Verdana, sans-serif;
  line-height: auto;
}

h1 {
  font-weight: bold;
  color: #000000;
}

h2, .heading, h2.minical {
  font-weight: normal;
  color: #000000;
  background-color: none;
}

h2.section_header {
  font-weight: bold;
  width: 100%;
  border-bottom: 1px solid #000000;
}

h2.pitch {
  text-align: center;
}

h3, .subheading {
  font-weight: bold;
  color: #000000;
}

h4, .label {
  font-weight: bold;
}

h5 {
  font-weight: bold;
}

a {
  color: #DC6328;
  text-decoration: none;
}
a:hover {
  color: #239cd7;
}

.views-field-title a {
  width: 65%;
  font-size: 1.6em;
  font-weight: bold;
  font-family: 'Fira', Helvetica, Arial, Verdana, sans-serif;
  line-height: auto;
}

#highlighted .views-field-title a {
  color: #DC6328;
}
#highlighted .views-field-title a:hover {
  color: #239cd7;
}

#main .views-field-title a {
  color: #000000;
}
#main .views-field-title a:hover {
  color: #DC6328;
}

p.heading {
  margin: 0;
}

.button, .button--primary, .button--secondary {
  font-size: 1.2em;
  font-weight: normal;
  color: #ffffff;
  padding: .5em;
  background-color: #DC6328;
  text-align: center;
}
.button.big, .big.button--primary, .big.button--secondary {
  margin: .5em auto;
}
.button.small, .small.button--primary, .small.button--secondary {
  width: 150px;
  margin: 0;
}

.button a, .button--primary a, .button--secondary a, a.button, a.button--primary, a.button--secondary {
  font-family: 'Fira', Helvetica, Arial, Verdana, sans-serif;
  line-height: auto;
  color: #ffffff;
}
.button a:hover, .button--primary a:hover, .button--secondary a:hover, a.button:hover, a.button--primary:hover, a.button--secondary:hover {
  color: #cccccc;
}

.clear {
  width: 0;
  height: 0;
  clear: both;
}

/*Collapsible Fieldsets*/
html.js fieldset.collapsible {
  border-bottom: 1px solid #333333;
}
html.js fieldset.collapsible legend:before {
  content: "\f0d7";
  font-family: FontAwesome;
  float: left;
  margin-left: 0;
  margin-right: 10px;
}
html.js fieldset.collapsible.collapsed legend:before {
  content: "\f0da";
}
html.js fieldset.collapsible legend {
  font-size: 1.2em;
  font-family: 'Fira', Helvetica, Arial, Verdana, sans-serif;
  line-height: auto;
  width: 100%;
}
html.js fieldset.collapsible .fieldset-wrapper {
  margin-bottom: 10px;
  margin-left: 0px;
}

/*Table Styles */
table {
  border: 0;
  color: #999999;
  font-family: 'Fira', Helvetica, Arial, Verdana, sans-serif;
  line-height: auto;
}
table tr td {
  border-bottom: 2px solid #999999;
  border-top: 0;
  border-left: 0;
  border-right: 0;
}

.fa-lg {
  font-size: 1.4em !important;
}

.region-header a {
  font-family: 'Fira', Helvetica, Arial, Verdana, sans-serif;
  line-height: auto;
  font-weight: normal;
  color: #ffffff;
  text-decoration: none;
  font-size: .75em;
}
.region-header a:hover {
  color: #e8e8e8;
}

.navigation a, .navigation .nolink, .navigation .menu a.active {
  font-family: 'Fira', Helvetica, Arial, Verdana, sans-serif;
  line-height: auto;
  color: #ffffff;
  text-decoration: none;
  font-size: .9em;
  cursor: pointer;
}
.navigation a:hover, .navigation .nolink:hover, .navigation .menu a.active:hover {
  color: #cccccc;
}

.region-blockgroup-feeds {
  max-width: 960px;
  margin-left: auto;
  margin-right: auto;
}

hr {
  border: 1px dotted #cccccc;
  border-style: none none dotted;
  color: none;
  background-color: none;
  margin-bottom: 1em;
}

/*Hide the time field across the site, let template file rewrite it by date*/
.field-name-field-time {
  display: none;
}

/*Pagination Controls*/
.item-list {
  float: left;
  margin-bottom: 1em;
  margin-left: 8px;
  color: #ffffff;
}
.item-list li {
  line-height: 1em;
}
.item-list a {
  color: #ffffff;
}
.item-list .pager-current {
  background-color: #ab2c27;
  padding: 1em;
}
.item-list .pager-item, .item-list .pager-next, .item-list .pager-last, .item-list .pager-first, .item-list .pager-previous {
  background-color: #000000;
  padding: 1em;
}

/*Collapsible Fieldset Styles */
html.js fieldset.collapsed {
  height: auto;
}

fieldset {
  padding-bottom: 0px;
}
fieldset .fieldset-wrapper {
  padding-top: 1em;
}

fieldset.collapsible, fieldset.collapsed {
  border-bottom: none;
  border-left: 0px;
  border-right: 0px;
  border-top: 0px;
}
fieldset.collapsible .collapsed .fieldset-legend, fieldset.collapsed .collapsed .fieldset-legend {
  background: none;
}

.fieldset-icons {
  float: left;
  margin-top: 1em;
  color: #e8e8e8;
}

.node-type-people-page .fieldset-icons {
  margin-top: .5em;
}

/*Event URL field on Calendar node edit page is wonky */
.form-item-field-event-url-und-0-url {
