/**
 * 1. Set default font family to sans-serif.
 * 2. Prevent iOS text size adjust after orientation change, without disabling
 *    user zoom.
 * 3. Correct text resizing oddly in IE 6/7 when body `font-size` is set using
 *    `em` units.
 */
html {
  font-family: Verdana, Tahoma, "DejaVu Sans", sans-serif;
  /* 1 */
  font-size: 100%;
  /* 3 */
  -ms-text-size-adjust: 100%;
  /* 2 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
  line-height: 1.45;
}

/* Remove default margin. */
body {
  margin: 0;
  padding: 0;
}

/**
 * Links
 *
 * The order of link states are based on Eric Meyer's article:
 * https://meyerweb.com/eric/thoughts/2007/06/11/who-ordered-the-link-states
 */
/* Address `outline` inconsistency between Chrome and other browsers. */
a:focus {
  outline: thin dotted;
}

/* Improve readability when focused and also mouse hovered in all browsers. */
a:active,
a:hover {
  outline: 0;
}

/**
 * Typography
 *
 * To achieve a pleasant vertical rhythm, we use Compass' Vertical Rhythm mixins
 * so that the line height of our base font becomes the basic unit of vertical
 * measurement. We use multiples of that unit to set the top and bottom margins
 * for our block level elements and to set the line heights of any fonts.
 * For more information, see https://24ways.org/2006/compose-to-a-vertical-rhythm
 */
/* Set 1 unit of vertical rhythm on the top and bottom margin. */
p,
pre {
  margin: 1.45 0;
}

blockquote {
  /* Also indent the quote on both sides. */
  margin: 1.45 30px;
}

/**
 * Address variable `h1` font-size and margin within `section` and `article`
 * contexts in Firefox 4+, Safari 5, and Chrome.
 */
h1 {
  /* Set the font-size and line-height while keeping a proper vertical rhythm. */
  font-size: 2.625em;
  line-height: 1.03571em;
  /* Set 1 unit of vertical rhythm on the top and bottom margins. */
  margin-top: 1.45;
  margin-bottom: 1.45;
}

h2 {
  font-size: 2em;
  line-height: 1.0875em;
  margin-top: 1.45;
  margin-bottom: 1.45;
}

h3 {
  font-size: 1.5em;
  line-height: 1.0875em;
  margin-top: 1.45;
  margin-bottom: 1.45;
}

h4 {
  font-size: 1em;
  line-height: 1.17812em;
  margin-top: 1.45;
  margin-bottom: 1.45;
}

h5 {
  font-size: 0.83em;
  line-height: 1.20105em;
  margin-top: 1.45;
  margin-bottom: 1.45;
}

h6 {
  font-size: 0.67em;
  line-height: 1.21735em;
  margin-top: 1.45;
  margin-bottom: 1.45;
}

