/* Move the Nav when hamburger menu fires */
@media (max-width: 834px) {
  .region-navigation {
    margin-top: 2em;
  }
}
@media (min-width: 835px) {
  .region-navigation {
    margin-top: 0em;
  }
}
/*top utility menu */
.header {
  background-color: #333333;
  color: #ffffff;
  height: 50px;
  padding: 10px 0;
  width: 100%;
  z-index: 1000;
  height: 47px;
}
.header ul {
  margin: 0;
  padding: 0 1em;
  height: 40px;
}
.header ul li {
  padding: 0;
  list-style-type: none;
  list-style-image: none;
  font-family: 'Fira', Helvetica, Arial, Verdana, sans-serif;
  line-height: auto;
  font-size: 1.2em;
  font-weight: normal;
  color: #ffffff;
  padding: .5em;
  background-color: #DC6328;
  text-align: center;
  display: inline;
  vertical-align: middle;
  white-space: nowrap;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  background-color: #DC6328;
}
.header ul li.first {
  float: left;
}
.header ul li.last {
  float: right;
}
.header ul a {
  display: block;
  height: 50px;
}

.top-fixed {
  position: fixed;
  top: 0px;
  display: block !important;
  opacity: 1;
}

/*Logo Block*/
#block-block-6 {
  float: left;
}
#block-block-6 img {
  height: auto;
  margin-top: -0.5em;
  margin-left: 1em;
}
@media (min-width: 835px) {
  #block-block-6 img {
    max-height: 70px;
  }
}
@media (max-width: 834px) {
  #block-block-6 img {
    max-height: 50px;
    margin-top: -0.2em;
  }
}

/*main menu */
span.nolink > .sf-sub-indicator {
  display: none;
}

.fixed {
  position: fixed;
  top: 50px;
  opacity: 1;
  transition: 0.4s ease;
  min-height: 70px;
}

#block-superfish-1 {
  white-space: nowrap;
  margin: 0 auto;
  width: 400px;
  padding-top: 1.4em;
  position: relative;
  text-align: center;
}
#block-superfish-1 li {
  padding: 0;
  list-style-type: none;
  list-style-image: none;
  font-family: 'Fira', Helvetica, Arial, Verdana, sans-serif;
  line-height: auto;
  font-size: 1.5em;
  font-weight: bold;
  display: table-cell;
  text-align: center;
  min-width: 60px;
  margin-right: 1em;
}
#block-superfish-1 li.last {
  margin-right: 0px;
}
#block-superfish-1 li ul {
  white-space: wrap;
  margin-top: -30px;
  margin-left: -41px;
  width: 150px !important;
  text-align: center;
}
#block-superfish-1 li ul li {
  display: table-cell;
  text-align: center;
  font-weight: normal;
  white-space: normal;
  margin: 0px;
  border-collapse: collapse;
  background-color: #ffffff;
}
#block-superfish-1 li ul li.first {
  -moz-border-radius-topleft: 5px;
  -moz-border-radius-topright: 5px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
#block-superfish-1 li ul li.last {
  -moz-border-radius-bottomleft: 5px;
  -moz-border-radius-bottomright: 5px;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}
#block-superfish-1 li ul li a {
  height: auto;
  color: #3f3f3f;
  font-size: .5em;
  font-weight: normal;
  padding: 10px;
}
#block-superfish-1 li ul li a:hover {
  color: #DC6328;
}

.sf-menu {
  float: left;
  position: relative;
  left: 50%;
}

.sf-menu ul li {
  left: 0%;
